@font-face {
  font-family: "ssp-regular";
  src: url("../../asset/font/source-sans-pro-regular.ttf");
}
@font-face {
  font-family: "ssp-light";
  src: url("../../asset/font/source-sans-pro-light.ttf");
}
@font-face {
  font-family: "ssp-italic";
  src: url("../../asset/font/source-sans-pro-italic.ttf");
}
@font-face {
  font-family: "ssp-regular";
  src: url("../../asset/font/source-sans-pro-regular.ttf");
}
@font-face {
  font-family: "ssp-light";
  src: url("../../asset/font/source-sans-pro-light.ttf");
}
@font-face {
  font-family: "ssp-italic";
  src: url("../../asset/font/source-sans-pro-italic.ttf");
}
h1, h2, h3, h4, h5, h6, label, p {
  margin: 0;
}

h1, h2, h3, h4, h5, h6, button {
  font-family: "ssp-regular", sans-serif;
  font-weight: bold;
}

label, p {
  font-family: "ssp-light", sans-serif;
  cursor: pointer;
}

input {
  font-family: "ssp-light", sans-serif;
}

* {
  box-sizing: border-box;
}

.sticky {
  position: sticky;
  top: -1px;
  background-color: #ffffff;
}